$banila_color: #FCA2CA;
$white : #ffffff;
$black: #333;
$dark: #4a4a4a;
$bg-input: #d9d9d9;
$line: #e6e6e6;
$btn-edit: #cfe3ef;
$txt: #222;

main {
  width: 80%;
  margin: 0 auto;
  padding: 70px 0 60px;
  font-family: "Noto Sans KR", "나눔고딕", sans-serif;
  color: $txt;
}

h2 {
  text-align: center;
  font-size: 26px;
  margin: 20px auto 50px auto;
}

.input_item {
  max-width: 760px;
  margin: 0 auto 70px;
  padding: 34px 34px 28px;
  border-radius: 18px;
  background: #fff;
  text-align: left;

  input {
    box-sizing: border-box;
  }

  p {
    display: block;
    align-items: center;
    justify-content: center;
    gap: 18px;
    margin: 18px 0;

    &:last-child {
      margin-top: 26px;
    }
  }

  label {
    display: block;
    width: auto;
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 800;
    letter-spacing: -0.02em;
  }

  input[type="text"] {
    width: 100%;
    height: 54px;
    padding: 0 16px;
    border: none;
    background: #f3f3f3;
    border-radius: 12px;
    font-size: 16px;
    outline: none;
    transition: border-color .15s ease, box-shadow .15s ease;

    &:focus {
      border-color: $banila_color;
      box-shadow: 0 0 0 4px rgba(252, 162, 202, .25);
      background: #fff;
    }
  }

  input[type="file"] {
    width: 100%;
    padding: 12px 14px;
    border-radius: 12px;
    border: 1px dashed #d8d8d8;
    background: #fafafa;
  }

  input[type="button"] {
    width: 100%;
    height: 64px;
    border-radius: 12px;
    background: $banila_color;
    color: #fff;
    font-size: 22px;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 10px;
    transition: 0.3s;

    &:hover {
      filter: brightness(.97);
    }

    &:active {
      transform: translateY(1px);
    }
  }
}

.product_list {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 60px 46px;

  li {
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: 100%;

    img {
      width: 100%;
      object-fit: cover;
      background: #cfcfcf;
      display: block;
    }

    .pname {
      font-size: 18px;
      font-weight: 800;
      line-height: 1.35;
      min-height: 52px;
      letter-spacing: -0.02em;
      margin-top: 6px;
      word-break: keep-all;
    }

    .pcost {
      font-size: 14px;
      color: #8a8a8a;
      text-decoration: line-through;
      text-align: right;
      margin-top: auto;
    }

    .pprice {
      font-size: 24px;
      font-weight: 900;
      color: $banila_color;
      text-align: right;
      letter-spacing: -0.02em;
    }

    .edit_box {
      display: flex;
      flex-direction: column;
      gap: 10px;
      margin-top: 6px;

      input {
        width: 90%;
        margin: 0 auto;
        height: 42px;
        padding: 0 12px;
        font-size: 15px;
        border-radius: 10px;
        border: 1px solid #e6e6e6;
        background: #fafafa;
        outline: none;
        transition: all 0.2s ease;

        &:focus {
          border-color: #fca2ca;
          background: #fff;
          box-shadow: 0 0 0 3px rgba(252, 162, 202, 0.25);
        }

        &[name="cost"],
        &[name="price"] {
          text-align: right;
          font-weight: 700;
        }
      }
    }

    .pbtns {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 14px;
      margin-top: 8px;

      input[type="button"] {
        height: 44px;
        border: none;
        cursor: pointer;
        background-color: $white;
        border-radius: 300px;
        font-weight: 800;
        font-size: 15px;
        border: 1px solid $btn-edit;
        transition: 0.3s;

        &:hover,
        &:active {
          border: none;
          outline: none;
          box-shadow: none;
        }

        &:hover {
          background-color: rgb(255, 207, 229);
        }

        &:active {
          filter: brightness(0.72);
        }
      }
    }
  }
}

.top_btn {
  position: fixed;
  right: 28px;
  bottom: 50px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: none;
  background: $banila_color;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.28);
  z-index: 999;
  transition: 0.3s;

  &:hover {
    filter: saturate(1.5);
  }

  &:active {
    transform: translateY(6px);
  }
}

@media (max-width: 960px) {
  main {
    width: 92%;
    padding: 50px 0;
  }

  .input_item {
    max-width: 100%;

    p {
      flex-direction: column;
      align-items: stretch;
      gap: 10px;
    }

    label {
      width: 100%;
      font-size: 18px;
    }

    input[type="text"],
    input[type="file"] {
      width: 100%;
    }

    input[type="button"] {
      width: 100%;
    }
  }

  .product_list {
    max-width: 100%;
    grid-template-columns: repeat(2, 1fr);
    gap: 46px;

  }
}

@media (max-width: 500px) {
  .product_list {
    grid-template-columns: 1fr;
  }
}