@charset "UTF-8";
main {
  width: 80%;
  margin: 0 auto;
  padding: 70px 0 60px;
  font-family: "Noto Sans KR", "나눔고딕", sans-serif;
  color: #222;
}

h2 {
  text-align: center;
  font-size: 26px;
  margin: 20px auto 50px auto;
}

.input_item {
  max-width: 760px;
  margin: 0 auto 70px;
  padding: 34px 34px 28px;
  border-radius: 18px;
  background: #fff;
  text-align: left;
}
.input_item input {
  box-sizing: border-box;
}
.input_item p {
  display: block;
  align-items: center;
  justify-content: center;
  gap: 18px;
  margin: 18px 0;
}
.input_item p:last-child {
  margin-top: 26px;
}
.input_item label {
  display: block;
  width: auto;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 800;
  letter-spacing: -0.02em;
}
.input_item input[type=text] {
  width: 100%;
  height: 54px;
  padding: 0 16px;
  border: none;
  background: #f3f3f3;
  border-radius: 12px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.15s ease, box-shadow 0.15s ease;
}
.input_item input[type=text]:focus {
  border-color: #FCA2CA;
  box-shadow: 0 0 0 4px rgba(252, 162, 202, 0.25);
  background: #fff;
}
.input_item input[type=file] {
  width: 100%;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px dashed #d8d8d8;
  background: #fafafa;
}
.input_item input[type=button] {
  width: 100%;
  height: 64px;
  border-radius: 12px;
  background: #FCA2CA;
  color: #fff;
  font-size: 22px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  margin-top: 10px;
  transition: 0.3s;
}
.input_item input[type=button]:hover {
  filter: brightness(0.97);
}
.input_item input[type=button]:active {
  transform: translateY(1px);
}

.product_list {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 60px 46px;
}
.product_list li {
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 100%;
}
.product_list li img {
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  background: #cfcfcf;
  display: block;
}
.product_list li .pname {
  font-size: 18px;
  font-weight: 800;
  line-height: 1.35;
  min-height: 52px;
  letter-spacing: -0.02em;
  margin-top: 6px;
  word-break: keep-all;
}
.product_list li .pcost {
  font-size: 14px;
  color: #8a8a8a;
  text-decoration: line-through;
  text-align: right;
  margin-top: auto;
}
.product_list li .pprice {
  font-size: 24px;
  font-weight: 900;
  color: #FCA2CA;
  text-align: right;
  letter-spacing: -0.02em;
}
.product_list li .edit_box {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 6px;
}
.product_list li .edit_box input {
  width: 90%;
  margin: 0 auto;
  height: 42px;
  padding: 0 12px;
  font-size: 15px;
  border-radius: 10px;
  border: 1px solid #e6e6e6;
  background: #fafafa;
  outline: none;
  transition: all 0.2s ease;
}
.product_list li .edit_box input:focus {
  border-color: #fca2ca;
  background: #fff;
  box-shadow: 0 0 0 3px rgba(252, 162, 202, 0.25);
}
.product_list li .edit_box input[name=cost], .product_list li .edit_box input[name=price] {
  text-align: right;
  font-weight: 700;
}
.product_list li .pbtns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 14px;
  margin-top: 8px;
}
.product_list li .pbtns input[type=button] {
  height: 44px;
  border: none;
  cursor: pointer;
  background-color: #ffffff;
  border-radius: 300px;
  font-weight: 800;
  font-size: 15px;
  border: 1px solid #cfe3ef;
  transition: 0.3s;
}
.product_list li .pbtns input[type=button]:hover, .product_list li .pbtns input[type=button]:active {
  border: none;
  outline: none;
  box-shadow: none;
}
.product_list li .pbtns input[type=button]:hover {
  background-color: rgb(255, 207, 229);
}
.product_list li .pbtns input[type=button]:active {
  filter: brightness(0.72);
}

.top_btn {
  position: fixed;
  right: 28px;
  bottom: 50px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: none;
  background: #FCA2CA;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.28);
  z-index: 999;
  transition: 0.3s;
}
.top_btn:hover {
  filter: saturate(1.5);
}
.top_btn:active {
  transform: translateY(6px);
}

@media (max-width: 960px) {
  main {
    width: 92%;
    padding: 50px 0;
  }
  .input_item {
    max-width: 100%;
  }
  .input_item p {
    flex-direction: column;
    align-items: stretch;
    gap: 10px;
  }
  .input_item label {
    width: 100%;
    font-size: 18px;
  }
  .input_item input[type=text],
  .input_item input[type=file] {
    width: 100%;
  }
  .input_item input[type=button] {
    width: 100%;
  }
  .product_list {
    max-width: 100%;
    grid-template-columns: repeat(2, 1fr);
    gap: 46px;
  }
}
@media (max-width: 500px) {
  .product_list {
    grid-template-columns: 1fr;
  }
}/*# sourceMappingURL=main.css.map */