$base_color: #242424;
$base_fontsize: 12px;

header {
  line-height: 60px;
  text-align: center;
  background: #464646;
  position: fixed;
  width: 100%;

  h1 img {
    width: 160px;
  }
}

footer {
  clear: both;
  background-color: $base_color;
  color: #fff;
  text-align: center;
  line-height: 40px;

  address {
    font-style: normal;
  }
}