$base_color: #242424;
$base_fontsize: 12px;

* {
  margin: 0;
  padding: 0;
}

ol,
ul,
li {
  list-style: none;
}

body {
  font-family: '나눔 고딕', '맑은 고딕', Arial, sans-serif;
  font-size: $base_fontsize;
  color: $base_color;
}

a {
  text-decoration: none;
  color: $base_color;
}